<template>
    <div class="card mx-auto tarjeta">
        <img :src="imageSrc" class="card-img-top imgP" alt="imgProyecto">
        <div class="card-body contentDiv">
            <h5 class="card-title">{{ nombre }}</h5>
            <p class="card-text pDescripcion">{{ descripcion }}</p>
        </div>
        <div class="card-body">
            <a :href="linkDeploy" target="_blank" class="card-link"><button class="btn">Link al proyecto</button></a>
            <a :href="linkGithub" target="_blank" class="card-link"><button class="btn">Link al código</button></a>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {

        }
    },
    props: {
        nombre: String,
        descripcion: String,
        imagen: String,
        linkDeploy: String,
        linkGithub: String
    },
    computed: {
        imageSrc() {
            try {
                return require(`@/assets/${this.imagen}`)
            } catch (error) {
                console.log('error: ', error);
                return '';
            }
        }
    }
}
</script>

<style scoped>
.tarjeta{
    width: 25rem;
    height: 32rem;
    background-color: #282828;
    color: aliceblue;
    border: 1px solid black;
}

.imgP{
    height: 240px;
}

/* .pDescripcion{

} */
 .contentDiv{
    height: 10rem
 }

 .btn{
    background-color: #42b983;
    color: white;
 }

 .btn:hover{
    background-color: #e1e1e1;
    color: rgb(22, 22, 22);
  }

  .btn{
    transition: transform 0.3s ease;
    cursor: pointer;
  }

  .btn:hover{
    transform: scale(1.1); /* Aumenta el tamaño al pasar el cursor */
  }
</style>