<template>
  <h1 class="p-3">Mis proyectos</h1>

  <div class="container row mx-auto">
    <div class="col-4 mx-auto m-3 divTarjeta" v-for="(item, index) in data" :key="index">
      <ProyectoComp 
        :nombre="item.nombre" 
        :descripcion="item.descripcion" 
        :imagen="item.imagen" 
        :linkDeploy="item.linkDeploy"
        :linkGithub="item.linkGithub"/>
    </div>
  </div>

</template>

<script setup>
import { computed, onMounted } from 'vue';
import { useStore } from 'vuex';
import ProyectoComp from '@/components/ProyectoComp.vue';

//accedemos al store de Vuex
const store = useStore();

// Computed property para obtener los datos desde Vuex
const data = computed(() => store.getters.getProyectos)

// Método para despachar la acción de Vuex
const loadData = async () => {
  await store.dispatch('fetchData');
};

// Llama a la acción cuando el componente se monta
onMounted(() => {
  loadData();
});

</script>

<style scoped>
.divTarjeta{
  transition: transform 0.3s ease;
}

.divTarjeta:hover{
  transform: scale(1.03); /* Aumenta el tamaño al pasar el cursor */
}
</style>